@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

* {
  font-family: "DM Sans", sans-serif;
  font-size: 16px;
  line-height: 150%;
  color: #555555;
}
a {
  display: inline-block;
}

body,
html {
  overflow-x: hidden;
}

.mb-big {
  margin-bottom: 25px;
}

.mb-medium {
  margin-bottom: 20px;
}

.mb-small, .footer__contact-info, .footer__link {
  margin-bottom: 10px;
}

.container {
  max-width: 1150px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.primary-title,
.title,
.small-title {
  font-weight: 700;
  color: #222222;
}

.primary-title {
  font-size: 56px;
}
@media screen and (max-width: 480px) {
  .primary-title {
    font-size: 42px;
  }
}

.sub-title {
  text-align: center;
}

.title {
  text-align: center;
  font-size: 48px;
}
@media screen and (max-width: 480px) {
  .title {
    font-size: 36px;
  }
}

.small-title {
  font-size: 24px;
}
.small-title_blue {
  color: #086cf9;
}
@media screen and (max-width: 480px) {
  .small-title {
    font-size: 21px;
  }
}

.button {
  transition: all ease 0.2s;
  font-size: 16px;
  padding: 15px 25px;
  background: #086cf9;
  color: #ffffff;
  border-radius: 5px;
}
.button:hover {
  background: #0d61d8;
}
@media screen and (max-width: 425px) {
  .button {
    padding: 10px 20px;
  }
}
.button_mobile {
  display: none;
}
@media screen and (max-width: 425px) {
  .button_mobile {
    display: block;
  }
}

.header {
  position: relative;
}

.link, .about-us__link {
  transition: all ease 0.2s;
  cursor: pointer;
  color: #222222;
}
.link:hover, .about-us__link:hover {
  color: #086cf9;
}

.navigation {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.navbar {
  position: relative;
  background: #ffffff;
  z-index: 3;
  padding: 30px 15px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
.navbar__link {
  margin: 0 20px;
}
.navbar__buttons {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
@media screen and (max-width: 768px) {
  .navbar {
    padding: 15px;
  }
}

@media screen and (max-width: 768px) {
  ._pc {
    display: none;
  }
}

@media screen and (min-width: 769px) {
  ._mobile {
    display: none;
  }
}

.burger {
  position: relative;
  width: 50px;
  height: 30px;
}
@media screen and (max-width: 768px) {
  .burger {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    flex-direction: row;
  }
}
.burger_active .burger__line {
  background: rgba(255, 255, 255, 0);
}
.burger_active .burger__line::after, .burger_active .burger__line::before {
  top: 50%;
}
.burger_active .burger__line::after {
  transform: rotate(45deg);
}
.burger_active .burger__line::before {
  transform: rotate(-45deg);
}
.burger__line {
  width: 100%;
  height: 3px;
  background: #222222;
}
.burger__line::after, .burger__line::before {
  transition: all ease 0.2s;
  content: "";
  position: absolute;
  background: #222222;
  left: 0;
  height: 3px;
  width: 100%;
}
.burger__line::after {
  top: 0;
}
.burger__line::before {
  bottom: 0;
}

.burger-menu {
  transition: all ease 0.2s;
  position: absolute;
  width: 100%;
  top: -100%;
  transform: translateY(-100%);
  left: 0;
  z-index: 1;
  background: #ffffff;
  padding: 15px;
}
.burger-menu a {
  margin: 0;
  margin-bottom: 10px;
}
.burger-menu .navigation {
  flex-direction: column;
  align-items: flex-end;
  margin-bottom: 15px;
}
.burger-menu .navbar__buttons {
  flex-direction: column;
  align-items: flex-end;
}
.burger-menu_active {
  top: 100%;
  transform: none;
}

.img {
  position: relative;
}
.img img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main-screen {
  background: url("../img/main-screen.png");
  height: 740px;
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
@media screen and (max-width: 768px) {
  .main-screen {
    height: 560px;
  }
}
.main-screen__body {
  padding-bottom: 100px;
  text-align: center;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background: rgba(34, 34, 34, 0.662745098);
  color: #ffffff;
}
.main-screen__body .primary-title,
.main-screen__body p {
  color: white;
}
@media screen and (max-width: 768px) {
  .main-screen__body {
    padding: 0;
  }
}
.main-screen__img {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.about-us {
  background: #ffffff;
  box-shadow: 0px 11px 35px rgba(0, 0, 0, 0.08);
  padding: 50px 80px;
  border-radius: 10px;
  transform: translateY(-50%);
  margin-bottom: 30px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
@media screen and (max-width: 768px) {
  .about-us {
    transform: none;
    justify-content: center;
    padding: 20px;
  }
}
@media screen and (max-width: 650px) {
  .about-us {
    flex-direction: column;
  }
}
.about-us__item {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  margin-right: 15px;
  width: max-content;
  margin-bottom: 30px;
}
.about-us__logo {
  width: 90px;
  height: 90px;
  margin-right: 25px;
}
.about-us__logo img {
  object-fit: contain;
}
.about-us__number {
  font-size: 26px;
  font-weight: 700;
}
.about-us__link {
  color: #086cf9;
}
.about-us__link:hover {
  color: #0d61d8;
}

.find-course {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: stretch;
  flex-direction: row;
  margin-bottom: 130px;
}
@media screen and (max-width: 768px) {
  .find-course {
    flex-direction: column-reverse;
  }
  .find-course__img {
    height: 300px;
  }
}
.find-course .title {
  text-align: left;
}
.find-course__content {
  max-width: 50%;
  width: 100%;
  margin-right: 20px;
}
.find-course__content p {
  flex: 1 1 100%;
}
@media screen and (max-width: 768px) {
  .find-course__content {
    max-width: 100%;
  }
}
.find-course__img {
  width: 100%;
  border-radius: 10px;
  overflow: hidden;
}

.flex-space {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.card-icon {
  width: 80px;
  height: 80px;
}
.card-icon img {
  object-fit: contain;
}

.how-work {
  margin-bottom: 130px;
}
.how-work__card {
  width: 270px;
  text-align: center;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.how-work__grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  justify-items: center;
  gap: 20px;
}
@media screen and (max-width: 1024px) {
  .how-work__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 768px) {
  .how-work__grid {
    grid-template-columns: 1fr;
  }
}

.all-course {
  background: #ecf4ff;
  padding: 60px 0;
}

.all-course {
  margin-bottom: 130px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.all-course__flex {
  justify-content: center;
  flex-wrap: wrap;
}
.all-course__card {
  border-radius: 10px;
  overflow: hidden;
  width: 320px;
  margin: 0 20px;
  background: #ffffff;
  padding: 25px;
  margin-bottom: 30px;
}
.all-course__img {
  border-radius: 5px;
  overflow: hidden;
  height: 200px;
  width: 100%;
}
.all-course__panel {
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
.all-course__panel-item {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: flex-end;
  flex-direction: row;
}
.all-course__panel-icon {
  margin-right: 20px;
  height: 25px;
  width: 25px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.all-course__line {
  background: #555555;
  opacity: 0.2;
  width: 100%;
  height: 2px;
}
.all-course__titles {
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.our-mentor {
  margin-bottom: 130px;
}
.our-mentor__flex {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
@media screen and (max-width: 1024px) {
  .our-mentor__flex {
    flex-direction: column;
  }
}
.our-mentor__card {
  margin: 0 20px 20px;
  border-radius: 5px;
  overflow: hidden;
  width: 340px;
  box-shadow: 0px 11px 55px rgba(0, 0, 0, 0.13);
}
@media screen and (max-width: 1024px) {
  .our-mentor__card {
    margin-bottom: 40px;
  }
}
.our-mentor__img {
  height: 340px;
}
.our-mentor__card-content {
  padding: 25px;
}
.our-mentor__card-info {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
}
.our-mentor__info-item {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  margin-right: 20px;
}
.our-mentor__card-icon {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}

.statistics {
  margin-bottom: 130px;
  background: #ecf4ff;
  padding: 50px 0;
}
@media screen and (max-width: 425px) {
  .statistics .flex-space {
    flex-direction: column;
    align-items: center;
  }
}
@media screen and (max-width: 425px) {
  .statistics__item {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
}

.reviews {
  position: relative;
  margin-bottom: 130px;
}
.reviews__card {
  box-shadow: 0px 13px 20px rgba(0, 0, 0, 0.08);
  border-radius: 5px;
  overflow: hidden;
  padding: 15px;
}
.reviews__slider-nav {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-end;
  align-items: center;
  flex-direction: row;
}
.reviews__person {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
}
.reviews__img {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}
.swiper {
  position: relative;
  overflow: visible;
  overflow-x: hidden;
  padding: 20px;
}

.button-prev,
.button-next {
  cursor: pointer;
  background: #086cf9;
  width: 50px;
  height: 50px;
  padding: 10px;
  border-radius: 100%;
}
.button-prev svg,
.button-next svg {
  width: 100%;
  height: 100%;
  fill: #ffffff;
}
@media screen and (max-width: 768px) {
  .button-prev,
.button-next {
    display: none;
  }
}

.button-prev {
  margin-right: 20px;
}

.subscribe-us {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #086cf9;
  margin-bottom: 130px;
  border-radius: 10px;
  padding: 50px 30px;
}
.subscribe-us p,
.subscribe-us h2 {
  color: #ffffff;
}
.subscribe-us__input-warpper {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  padding: 5px 10px;
  width: max-content;
  border-radius: 5px;
  background-color: #ffffff;
}
@media screen and (max-width: 768px) {
  .subscribe-us__input-warpper {
    padding: 5px;
  }
}
.subscribe-us .input {
  margin-right: 5px;
  max-width: 350px;
  width: 100%;
  font-size: 24px;
}
@media screen and (max-width: 425px) {
  .subscribe-us .input {
    max-width: 300px;
  }
}
@media screen and (max-width: 320px) {
  .subscribe-us .input {
    width: 200px;
  }
}
@media screen and (max-width: 425px) {
  .subscribe-us .button {
    display: none;
  }
}
.subscribe-us .button_mobile {
  fill: #ffffff;
  padding: 0;
  width: 35px;
  padding: 5px;
  height: 35px;
}
@media screen and (max-width: 425px) {
  .subscribe-us .button_mobile {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    flex-direction: row;
  }
}

.footer {
  width: 100%;
  background: #ecf4ff;
  padding: 39px 0;
}
.footer svg {
  width: 25px;
  height: 25px;
}
.footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
}
.footer__item {
  max-width: 360px;
}
.footer__social {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
}
.footer__social-item {
  background: #ffffff;
  padding: 10px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  border-radius: 5px;
  margin-right: 10px;
  height: min-content;
  fill: #086cf9;
}
.footer__link {
  display: block;
}
.footer__contact-info {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
}
.footer__contact-info svg {
  margin-right: 10px;
}